<template>
    <div>
        <img src="../../assets/img/vue-resource.png">
        <h1 class="mt10">{{msg}}</h1>
        <ul>
            <li v-for="article in articles">
                <div class="inl-block"><img :src="article.images.small"></div>
                <div class="m-content inl-block">
                    <div>{{article.title}}</div>
                    <div>年份：{{article.year}}</div>
                    <div>类型：{{article.subtype}}</div>
                </div>
            </li>
        </ul>
    </div>  
</template>

<script>
// mounted 钩子函数  这里去请求豆瓣数据
export default {
    data() {
        return {
            msg: '电影列表',
            articles: []
        }
    },
    created: function() { //这里mounted和created生命周期函数区别
        this.$http.jsonp('https://api.douban.com/v2/movie/top250?count=5', {}, {
            headers: {

            },
            emulateJSON: true
        }).then(function(res) {
            // 这里是处理正确的回调
            this.articles = res.data.subjects
            // this.articles = res.data["subjects"] 也可以
        }, function(res) {
            // 这里是处理错误的回调
            console.log(res)
        });
    }
}
</script>

<style scoped>
ul{
    list-style: none;
    margin: 0;
    padding: 0;
}

ul li{
    /* border-bottom: 1px solid #999; */
    padding: 10px 0;
}

.inl-block{
    display: inline-block;
}

.m-content{
    margin-left: 20px;
}
</style>


